import { request } from '@/utils/http'
import { getEvnBaseUrl } from '@/utils'

let baseURL = getEvnBaseUrl()

/**
 * @desc 获取上传地址
 */
export const getUploadURL = () => `${getEvnBaseUrl()}/file/upload`

/**
 * @desc 下载文件
 */
export function downloadFile(id) {
  return window.open(`${baseURL}/downloadFile?id=${id}`)
}

/**
 * @desc 上传文件
 */
export const uploadFile = (params) =>
  request.upload({
    url: '/file/upload',
    dataType: 'json',
    headers: {
      'content-type': 'multipart/form-data',
    },
    ...(params || {}),
  })

/**
 * @desc 上传图片
 */
export const uploadPic = (params) =>
  uni.uploadFile({
    url: `${baseURL}/img/upload`,
    formData: {
      size: 5,
    },
    name: 'image',
    ...(params || {}),
  })
